import React from "react";
import { useDispatch } from "react-redux";
import { MDBChip } from "mdbreact";
import { addItemAction } from "../../actions";
export default function ProductSimple(props) {
  const item = props.item;
  let dia = item.attributes.find(sel => sel.name === "Dias");
  const dispatch = useDispatch();
  if (dia) {
    dia = dia.options[0];
  } else {
    dia = "";
  }
  let genero = item.attributes.find(sel => sel.name === "GÃªnero");
  if (genero) {
    genero = genero.options[0];
  }

  function comprar() {
    return dispatch(addItemAction(item));
  }

  function btComprar() {
    return (
      <MDBChip
        bgColor="blue lighten-4"
        waves
        className="w-100"
        onClick={comprar}
      >
        Comprar
      </MDBChip>
    );
  }
  function btCamarote() {
    return (
      <div className="d-flex flex-column grey lighten-4 rounded border border-light p-2 m-2 justify-content-cente">
        <div>{dia}</div>
        <div>{genero}</div>
        <div>{item.price}</div>
        {btComprar()}
      </div>
    );
  }
  function btBloco() {
    return (
      <div className="d-flex flex-column grey lighten-4 rounded border border-light p-2 m-2 justify-content-cente">
        <div>{dia}</div>
        <div>{item.price}</div>
        {btComprar()}
      </div>
    );
  }
  function btPassaporte() {
    return (
      <div className="d-flex flex-column grey lighten-4 rounded border border-light p-2 m-2 justify-content-cente">
        <div>{item.name}</div>
        <div>{item.price}</div>
        {btComprar()}
      </div>
    );
  }
  switch (item.categories[0].id) {
    case 16: //camarote
      return btCamarote();
    case 17: //bloco
      return btBloco();
    case 19: //passaporte
      return btPassaporte();

    default:
      return <p>{item.name}</p>;
  }
}
